@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');

$jromeroPrimaryColor: #e4e0e1;
$jromeroSecondaryColor: #d6c0b3;
$jromeroAccentColor: #ab886d;
$jromeroBkgndColor: #493628;

:root {
  --jromeroPrimaryColor: #{$jromeroPrimaryColor};
  --jromeroSecondaryColor: #{$jromeroSecondaryColor};
  --jromeroAccentColor: #{$jromeroAccentColor};
  --jromeroBkgndColor: #{$jromeroBkgndColor};
}

html {
  font: 10pt/1 "Roboto", Arial, Helvetica, sans-serif;
}

html,
body {
  height: 100%;
  background-color: var(--jromeroBkgndColor);
}

html,
body,
article,
div,
footer,
h1,
h2,
h3,
header,
input,
label,
li,
main,
nav,
p,
section,
span,
ul {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

img,
img:focus {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: bottom;
}

body {
  color: var(--jromeroSecondaryColor);
  line-height: 1;
}
