/* vim: set tabstop=2 softtabstop=0 expandtab shiftwidth=2 smarttab : */
div#root:first-of-type {
  > footer {
    align-items: center;
    background-color: var(--jromeroBkgndColor);
    display: flex;
    flex-direction: column;
    font-size: 75%;
    //margin-top: 40px;
    padding: 20px 0;

    > div.jromero-logo:first-of-type {
      width: 250px;
      height: 40px;
      font-size: 0;
    }

    > div.jromero-logo:first-of-type::before {
      background-color: var(--jromeroSecondaryColor);
      display: block;
      height: 100%;
      mask-position: top;
    }

    > p {
      color: var(--jromeroPrimaryColor);
    }
  }

  @media (max-width: 768px) {
    > footer {
      font-size: 75%;
    }
  }
}
