div#root:first-of-type {
  display: flex;
  flex-direction: column;
  height: 100%;

  > header, > footer {
    flex-basis: auto;
    flex-grow: 0;
    flex-shrink: 0;
  }

  > main {
    flex-basis: auto;
    flex-grow: 1;
    flex-shrink: 0;
  }

  > header, > main {
    margin: 0 auto;
    width: 100%;
    max-width: 1240px;
  }

  .jromero-logo::before {
    content: '';
    display: inline-block;
    mask-image: url('/logo.svg');
    mask-position: center;
    mask-repeat: no-repeat;
    mask-size: contain;
  }

  main {
    section:not(.plain) {
      display: flex;
      flex-direction: column;
      justify-content: center;
      position: relative;

      > h2:first-of-type {
        //background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0.5) 100%);
        background-image: repeating-linear-gradient(
          -45deg,
          var(--jromeroAccentColor),
          var(--jromeroAccentColor) 5px,
          var(--jromeroBkgndColor) 5px,
          var(--jromeroBkgndColor) 50px
        );
        background-position: top, top 50% center;
        background-repeat: no-repeat, no-repeat;
        background-size: auto, 100%;
        border-radius: 25px;
        color: var(--jromeroPrimaryColor);
        display: block;
        font-size: 300%;
        height: 223px;
        letter-spacing: 0.1em;
        line-height: 223px;
        margin: 30px 0 0 0;
        text-align: center;
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

        > div:first-child {
          background-color: var(--jromeroBkgndColor);
          display: inline-block;
          line-height: 1;
          padding: 10px;
          vertical-align: middle;
        }
      }
    }
  }
}

div#root:first-of-type {
  @media (max-width: 768px) {
    > header, > main {
      max-width: 335px;
    }

    main {
      section:not(.plain) {
        > h2:first-of-type {
          border-radius: 10px;
          font-size: 112.5%;
          height: 111px;
          line-height: 111px;
          margin: 10px 0 0 0;
        }
      }
    }
  }
}
